#page-header {
	height: 130px;
	background: #2f4882;
	display: flex;
	box-shadow: 0px 1px 2px #111;
	z-index: 100;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}

#page-header .logo img {
	width: 342px;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	padding: 24px 0 24px 20px;
}

.header-nav {
	display: flex;
    align-items: center;
    flex: 2 1;
}

.menu {
	width: 220px;
	padding: 80px 40px 35px;
	cursor: pointer;
	color: white;
	display: flex;
	align-items: center;
	border-left: 2px solid #455c96;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}

.menu--active,
.menu:hover {
	background-color: #455c96;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}

.header-nav a {
	background-color: #1c2741;
    align-items: center;
    justify-content: center;
    padding: 90px 40px 21px;
    font-size: 1rem;
    font-weight: 400;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
}

.header-nav a:hover {
	background-color: #455c96;
	-webkit-transition: background-color 0.5s ease;
	transition: background-color 0.5s ease;
}

.menu--logo img {
	width: 70px;
	height:70px;
	margin-right:15px;
}

.menu--label {
	font-size: 20px;
    text-overflow: ellipsis;
    min-width: 0;
    overflow: hidden;
    white-space: nowrap;
}

/* Dropdown */

.menu-dropdown--wrapper {
	display: none;
}

.menu-dropdown--wrapper.menu-dropdown--active {
	display: block;
}

.menu-dropdown {
	width: 300px;
    position: absolute;
    background: #DDD;
    border: 1px solid #333;
    box-shadow: 0px 1px 3px black;
    right: 0px;
    top: 131px;
    z-index: 0;
}

.menu-mask {
	position: absolute;
    z-index: -10;
    background: rgba(0,0,0,0.5);
    width: 100%;
    top: 130px;
    left: 0px;
}

.menu-dropdown ul {
	list-style: none;
	width: 100%;
	padding: 0;
	margin: 0;
}

.menu-dropdown li a {
    width: 90%;
    padding: 20px 5%;
    border-bottom: 1px solid #AAA;
    display: block;
    text-decoration: none;
    color: #333;
    font-size: 16px;
    font-weight: 700;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.menu-dropdown li a:hover {
	background-color: #BBB;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}

.menu-dropdown li.logout a {
	border-bottom: none;
	border-top: 1px solid #AAA;
	color: red;
	margin-top: 80px;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	background-color: inherit;
	text-align: center;
}

.menu-dropdown li.logout a:hover {
	color: white;
	background-color: red;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}

.menu-dropdown .reviewing {
	padding: 20px;
    text-align: center;
    border-bottom: 1px solid #AAA;
    font-weight: bold;
    color: #FFFFFF;
    background: #455c96;
}


@media only screen and (max-width: 480px) {
	#page-header {
		height: 65px;
		-webkit-transition: all 0.2s ease;
		transition: all 0.2s ease;
	}

	#page-header .logo img {
		width: 175px;
		-webkit-transition: all 0.2s ease;
		transition: all 0.2s ease;
    	padding: 14px 0 14px 10px;
	}

	.header-nav {
		display: none;
	}

	.menu {
		width: auto;
		padding: 20px 15px;
		margin-left: auto;
		-webkit-transition: all 0.2s ease;
		transition: all 0.2s ease;
	}

	.menu--label {
		display: none;
	}

	.menu img {
		width: 45px;
		height: 45px;
		margin: 0;
	}

	.menu-dropdown {
		border: 0;
		width: 100%;
		top: 65px;
	}
}
