.pendingMembers-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.pendingMember {
	background: #DDD;
    display: flex;
    flex: 1 1;
    max-width: 49%;
    margin-bottom: 20px;
    align-items: center;
    border: 1px solid #AAA;
    box-shadow: 0px 2px 3px #111;
    height: 75px;
	position: relative;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.pendingMember .member-logo {
	display: flex;
	margin-right: 15px;
    border-right: 1px solid #666;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.pendingMember .member-logo img {
	width: 75px;
	height: 75px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.pendingMember .member-mobileWrapper {
	display: flex;
	flex: 1 1;
}

.pendingMember .member-info {
	display: flex;
	flex: 1 1;
	align-items: center;
	color: #333;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.pendingMember .member-info a {
	margin-left: 0px;
    position: absolute;
    left: 0px;
    bottom: -1px;
    width: 75px;
    height: 75px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.pendingMember .member-info a img {
	background: #FFF;
	position: absolute;
	right: 0;
	bottom: 0;
}

.pendingMember .member-action img {
	width: 30px;
    margin: 0 4px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

